@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- Centered and Robust Google Translate CSS --- */

/* 1. Force the top banner iframe to be hidden */
iframe.goog-te-banner-frame {
    display: none !important;
}

/* 2. Prevent the body from being pushed down by the (now hidden) banner */
body {
    top: 0px !important;
    position: relative !important;
}

/* 3. Style the main translation bar container to be centered */
#translation-bar-container {
    display: flex;
    justify-content: center; /* This will center the dropdown horizontally */
    align-items: center;
    padding: 0.25rem;
    background-color: #1F2937; /* gray-800 */
}
.dark #translation-bar-container {
    background-color: #000; /* black */
}

/* 4. Create a "window" to show only the select dropdown */
#google_translate_element_container {
    height: 30px; /* Fixed height to contain the select element */
    overflow: hidden; /* This is the key to hiding unwanted elements */
}

/* 5. Style the widget container created by Google */
#google_translate_element_container .goog-te-gadget-simple {
    background-color: transparent !important;
    border: none !important;
    /* Move the widget up to align the select box inside our "window" */
    position: relative;
    top: -2px; /* Adjust this value to vertically center the select */
}

/* 6. Style the dropdown <select> element to make it visible and match your UI */
#google_translate_element_container select.goog-te-combo {
    cursor: pointer;
    background-color: #4B5563; /* Tailwind gray-600 */
    color: white;
    border: 1px solid #6B7280; /* Add a subtle border */
    border-radius: 4px;
    padding: 4px 8px;
    height: 28px;
    font-size: 0.75rem; /* text-xs */
}

/* 7. Style the language selection list that appears on click */
/* Google renders this in an iframe, so we target those elements globally */
body > .goog-te-menu-frame {
    box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
    border-radius: 0.5rem; /* Rounded corners */
}
body > .goog-te-menu-frame .goog-te-menu2 {
    background-color: #1F2937; /* gray-800 */
    border: 1px solid #4B5563; /* gray-600 */
}
body > .goog-te-menu-frame .goog-te-menu2-item,
body > .goog-te-menu-frame .goog-te-menu2-item-selected {
    color: #fff;
    padding: 0.5rem 1rem; /* More padding for easier tapping on mobile */
}
body > .goog-te-menu-frame .goog-te-menu2-item:hover {
    background-color: #374151; /* gray-700 */
}
